// app/dashboard/transactions/page.tsx
'use client';

import { useEffect, useState } from 'react';
import { apiFetch, isAuthenticated } from '@/src/lib/api/api-client';

export default function TransactionsPage() {
  const [txs, setTxs] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (!isAuthenticated()) return;
    (async () => {
      try {
        const res = await apiFetch('/payments?limit=50&offset=0');
        setTxs(Array.isArray(res?.transactions) ? res.transactions : []);
      } catch (e: any) {
        setError(e?.message || 'Failed to load transactions');
      } finally {
        setLoading(false);
      }
    })();
  }, []);

  return (
    <div className="space-y-6">
      <h1 className="text-2xl font-semibold">Transactions</h1>
      <p className="text-muted-foreground">Your recent activity.</p>

      {loading && <div className="bg-card text-card-foreground rounded-xl border p-6">Loading transactions...</div>}
      {error && <div className="bg-red-50 dark:bg-red-900/20 text-red-700 dark:text-red-300 border border-red-200 dark:border-red-800 rounded-xl p-4">{error}</div>}

      <div className="bg-card text-card-foreground rounded-xl border overflow-hidden">
        <table className="min-w-full">
          <thead>
            <tr className="bg-muted text-left text-sm text-muted-foreground">
              <th className="px-4 py-3">Date</th>
              <th className="px-4 py-3">Reference</th>
              <th className="px-4 py-3">Amount</th>
              <th className="px-4 py-3">Currency</th>
              <th className="px-4 py-3">Status</th>
            </tr>
          </thead>
          <tbody className="divide-y">
            {txs.map((t) => (
              <tr key={String(t.id)} className="text-sm">
                <td className="px-4 py-3">{String(t.created_at || t.initiated_at)}</td>
                <td className="px-4 py-3 font-mono">{String(t.reference)}</td>
                <td className="px-4 py-3">{String(t.amount)}</td>
                <td className="px-4 py-3">{String(t.currency)}</td>
                <td className="px-4 py-3">
                  <span className={`px-2 py-1 rounded-md text-xs ${String(t.status).toLowerCase() === 'completed' ? 'bg-green-100 text-green-700 dark:bg-green-900/30 dark:text-green-300' : String(t.status).toLowerCase() === 'failed' ? 'bg-red-100 text-red-700 dark:bg-red-900/30 dark:text-red-300' : 'bg-yellow-100 text-yellow-700 dark:bg-yellow-900/30 dark:text-yellow-200'}`}>{String(t.status)}</span>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}